ul {
    list-style: none;
    list-style-position: outside;
    padding: 0 0 0 32px;
    margin: 0;
    display: flex;
    flex-direction: column;
    grid-gap: 16px;
    gap: 16px;
}

li {
    display: list-item;
}

ul li::before {
    content: "–";
    display: inline-block;
    text-indent: -32px;
}

.Now h3 a, .Now ul li a {
    color: #000;
    -webkit-text-decoration: underline #d5d5d5;
            text-decoration: underline #d5d5d5;
}

.Now a {
    color: #808080;
    text-decoration: none;
}
